#heading {
    font-family: 'Courier New', Courier, monospace;
    text-align: center;
}

#card {
    display: flex;
    flex-direction: row;
    font-size: 40px;
    height: 300px;
    justify-content: center;
    align-items: center;
    background-color: violet;
    border-radius: 30px;
}

li{
    list-style-type: none;
    margin: 0 30px;
    text-align: center;
}

li a button{
    color: brown;
    font-weight: bold;
    text-transform: uppercase;
}